@font-face {
  font-family: 'Roboto';
  src: url("../fonts/roboto-light-webfont.woff2") format("woff2"), url("../fonts/roboto-light-webfont.woff") format("woff");
  font-weight: 300;
  font-style: normal; }

@font-face {
  font-family: 'Roboto';
  src: url("../fonts/roboto-medium-webfont.woff2") format("woff2"), url("../fonts/roboto-medium-webfont.woff") format("woff");
  font-weight: 500;
  font-style: normal; }

@font-face {
  font-family: 'Roboto';
  src: url("../fonts/roboto-black-webfont.woff2") format("woff2"), url("../fonts/roboto-black-webfont.woff") format("woff");
  font-weight: 900;
  font-style: normal; }

html, body {
  box-sizing: border-box;
  max-width: 100%;
  min-width: 320px;
  overflow-x: hidden;
  margin: 0;
  padding: 0;
  color: white;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px; }

a {
  text-decoration: none;
  margin: 0;
  padding: 0;
  color: white;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px; }

ul {
  list-style: none;
  padding: 0;
  margin: 0; }

*,
*::before,
*::after {
  box-sizing: border-box; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.main-nav {
  background-color: #1a143b; }

.main-nav__wrap {
  width: 100%;
  display: -webkit-box;
  display: flex;
  -webkit-box-pack: justify;
          justify-content: space-between;
  flex-wrap: wrap;
  background-color: #282251;
  -webkit-box-align: center;
          align-items: center; }

.main-nav__logo {
  width: 228px;
  background-image: url(../img/logo-header-base.svg);
  background-size: 100%;
  background-repeat: no-repeat;
  padding-top: 14px;
  padding-bottom: 9px; }

.main-nav__logo img {
  margin-left: 24px; }

.main-nav__menu-btn {
  position: relative;
  width: 32px;
  height: 24px;
  border: none;
  background-color: transparent;
  cursor: pointer;
  margin-right: 24px; }
  .main-nav__menu-btn::before, .main-nav__menu-btn::after {
    content: '';
    position: absolute;
    width: 32px;
    height: 3px;
    top: 50%;
    left: 0;
    background-color: white;
    -webkit-transform: translate(-50%);
            transform: translate(-50%); }
  .main-nav__menu-btn::before {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg); }
  .main-nav__menu-btn::after {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg); }
  .main-nav__menu-btn:focus, .main-nav__menu-btn:hover {
    outline: none; }
    .main-nav__menu-btn:focus::after, .main-nav__menu-btn:focus::before, .main-nav__menu-btn:hover::after, .main-nav__menu-btn:hover::before {
      background-color: #ccc6ee; }
  .main-nav__menu-btn:active::after, .main-nav__menu-btn:active::before {
    background-color: #9891cf; }
  .main-nav__menu-btn::after {
    box-shadow: 0 0px 0 white; }
  .main-nav--closed .main-nav__menu-btn::before, .main-nav--closed .main-nav__menu-btn::after {
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%); }
  .main-nav--closed .main-nav__menu-btn::after {
    top: 0;
    box-shadow: 0 10px 0 white; }
  .main-nav--closed .main-nav__menu-btn::before {
    top: 20px; }
  .main-nav--closed .main-nav__menu-btn:focus::after, .main-nav--closed .main-nav__menu-btn:hover::after {
    box-shadow: 0 10px 0 #ccc6ee; }
  .main-nav--closed .main-nav__menu-btn:active::after {
    box-shadow: 0 10px 0 #9891cf; }

.main-nav__wrap-menu {
  padding-left: 24px;
  padding-right: 24px;
  padding-bottom: 55px; }
  .main-nav--closed .main-nav__wrap-menu {
    display: none; }

.main-nav__list {
  width: 100%;
  padding-top: 80px;
  padding-bottom: 45px; }

.main-nav__item {
  display: block;
  padding: 12px;
  text-align: center; }

.main-nav__link {
  position: relative;
  display: inline;
  font-size: 24px;
  line-height: 28px;
  opacity: 0.72; }
  .main-nav__link:focus, .main-nav__link:hover {
    opacity: 0.85; }
  .main-nav__link:active {
    opacity: 1; }
  .main-nav__link:not([href]) {
    opacity: 1; }
    .main-nav__link:not([href])::before {
      content: "";
      position: absolute;
      width: 100%;
      height: 2px;
      bottom: -4px;
      left: 0;
      opacity: 1;
      background-color: #ec174f; }

.main-nav__uzer-initials {
  display: none; }

.main-nav__enter,
.main-nav__account {
  margin: 0;
  padding: 0;
  color: white;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  display: block;
  width: 100%;
  padding: 9px;
  border: none;
  text-align: center;
  border-radius: 5px;
  background-color: #f8104d;
  color: white;
  cursor: pointer; }
  .main-nav__enter:focus, .main-nav__enter:hover,
  .main-nav__account:focus,
  .main-nav__account:hover {
    opacity: 0.6; }
  .main-nav__enter:active,
  .main-nav__account:active {
    opacity: 0.3; }

@media (min-width: 1160px) {
  .main-nav__uzer-initials {
    display: none; }
  .main-nav__uzer-initials {
    display: block;
    text-align: center;
    padding-top: 4px;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    background-color: #00a5ff; } }

.project {
  background-color: #1a143b; }

.project__about-company {
  margin-bottom: 55px; }

.project__competition {
  margin-bottom: 55px; }
